.fullscreen {
    position: relative;
    width: 100vw;
    height: 100vh;
}

.home {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-template-rows: 1fr 1fr .1fr;
    position: relative;
    background-color: black;
    width: 100vw;
    height: 100vh;
    grid-template-areas: 
        'topcont top1 top2'
        'bottomcont moreabout bottom1'
        'footer footer footer';
}

.home .topcont {
    grid-area: topcont;
    text-align: center;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas: 
        't'
        'm'
        'b';
}

.home .topcont h1{
    font-size: 5em;
}
.home .topcont h3{
    font-size: 3em;
}
.home .topcont h5{
    font-size: 1.2em;
}

.home .topcont .mid {
    grid-area: m;
    background-color: black;
}

.home .topcont .bot {
    grid-area: b;
    background-color: black;
}

.home .top1 {
    grid-area: top1;
    background-color: #d72323;
    color: black;
    transition: 0.3s;
    font-size: 3em;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        'logo'
        'text';
}

.home .top1:hover {
    background-color: black;
    color: #d72323;
}

.home .top2 {
    grid-area: top2;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        'top'
        'bottom';
}

.home .top2 .topLink {
    grid-area: top;
    font-size: 3em;
    color: #f5eded;
    background-color: black;
}

.home .top2 .topLink:hover {
    background-color: #f5eded;
    color: black;
}

.home .top2 .bottomLink {
    background-color: black;
    grid-area: bottom;
    font-size: 3em;
    color: #f5eded;
}

.home .top2 .bottomLink:hover {
    background-color: #f5eded;
    color: black;
}

.home .bottomcont {
    grid-area: bottomcont;
    text-align: center;
    display: grid;
    font-size: 1.2em;
    background-color: black;
    grid-template-rows: .75fr 1fr .75fr;
    grid-template-areas: 
        'contenttop'
        'contentmid'
        'contentbot';
}

.home .bottomcont .content {
    grid-area: contentmid;
    color: #888888;
}


.home .moreabout {
    grid-area: moreabout;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        'about'
        'cv';
}


.home .moreabout .about {
    grid-area: about;
    font-size: 3em;
    transition: .3s;
    background-color: black;
}

.home .moreabout .about:hover {
    background-color: #f5eded;
    color: black;
}

.home .moreabout .cv {
    grid-area: cv;
    font-size: 3em;
    transition: 0.3s;
    background-color: black;
}

.home .moreabout .cv a {
    text-decoration: none;
    color: #f5eded;
}

.home .moreabout .cv:hover {
    background-color: #f5eded;
    color: black;
    transition: .3s;
}

.home .bottom1 {
    grid-area: bottom1;
    background-color: #f5eded;
    color: black;
    transition: .3s;
    font-size: 3em;
}

.home .bottom1:hover {
    background-color: black;
    color: #f5eded;
}

.home footer {
    grid-area: footer;
    text-align: center;
    background-color: #3e3636;
    display: grid;
    font-size: 1.2em;
    grid-template-rows: .75fr 1fr .75fr;
    grid-template-areas: 
        'footertop'
        'footermid'
        'footerbot';
}

.home .footer {
    grid-area: footermid;
}

.page {
    background-color: #000000;
    color: white;
    font-family: 'Comfortaa';
    position: relative;
    width: 100vw;
    height: 100vh;
}

/* Smartphones (landscape) ----------- */
@media only screen 
and (min-width : 321px) {
    .home {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr .1fr;
        grid-template-areas: 
            'topcont topcont'
            'bottomcont bottomcont'
            'top1 top2'
            'moreabout bottom1'
            'footer footer';
    }
}

/* Smartphones (portrait) ----------- */
@media only screen 
and (max-width : 320px) {
    .home {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr .1fr;
        grid-template-areas: 
            'topcont'
            'bottomcont'
            'top1'
            'top2'
            'moreabout'
            'bottom1'
            'footer';
    }
}

/* iPads (landscape) ----------- */
@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px) 
and (orientation : landscape) {
    .home {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr .1fr;
        grid-template-areas: 
            'topcont topcont'
            'bottomcont bottomcont'
            'top1 top2'
            'moreabout bottom1'
            'footer footer';
    }
}

/* iPads (portrait) ----------- */
@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px) 
and (orientation : portrait) {
    .home {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr .1fr;
        grid-template-areas: 
            'topcont'
            'bottomcont'
            'top1'
            'top2'
            'moreabout'
            'bottom1'
            'footer';
    }
}

/* Desktops and laptops ----------- */
@media only screen 
and (min-width : 1224px) {
    .home {
        grid-template-columns: 2fr 1fr 1fr;
        grid-template-rows: 1fr 1fr .1fr;
        grid-template-areas: 
            'topcont top1 top2'
            'bottomcont moreabout bottom1'
            'footer moreabout bottom1';
    }
}

/* Large screens ----------- */
@media only screen 
and (min-width : 1824px) {
    .home {
        grid-template-columns: 2fr 1fr 1fr;
        grid-template-rows: 1fr 2fr .1fr;
        grid-template-areas: 
            'topcont top1 top2'
            'bottomcont moreabout bottom1'
            'footer moreabout bottom1';
    }
}

::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-thumb {
    background: #888;
}
::-webkit-scrollbar-thumb:hover {
    background: #555;
} 